/* eslint-disable react/forbid-prop-types */
import React, { useState } from 'react';
import { Box, Text, Link } from 'theme-ui';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import Carousel from './Carousel';
import Logo from './Logo';
import carouselData from '../assets/carouselData.json';
import Header from './Header';
import RegistrationForm from './RegistrationForm';
import Title from './Title';

const Home = ({
  setMessageState, userDetails, setUserDetails, apiUrl,
}) => {
  const { t } = useTranslation();
  const [carouselIndex, setCarouselIndex] = useState(0);
  const [carouselWidth, setCarouselWidth] = useState(0);

  const advanceSlider = () => {
    setCarouselIndex((index) => (index + 1 >= carouselData.length ? 0 : index + 1));
  };

  return (
    <Box
      sx={{
        height: '100%',
        position: 'relative',
        display: ['block', 'block', 'grid'],
        gridTemplateColumns: '5fr 11fr',
        gridTemplateAreas: '\'carousel form\'',
        '@media all and (-ms-high-contrast: none), (-ms-high-contrast: active)': {
          display: '-ms-grid',
          msGridColumns: '5fr 2fr 9fr',
        },
      }}
    >
      <Logo />
      <Carousel
        slides={carouselData}
        activeIndex={carouselIndex}
        setActiveIndex={setCarouselIndex}
        carouselWidth={carouselWidth}
        setCarouselWidth={setCarouselWidth}
        advanceSlider={advanceSlider}
      />
      <Box
        pl={[5, 6, 8, 9]}
        pr={[5, 6, 0]}
        sx={{
          flex: 1,
          overflow: 'auto',
          gridArea: 'form',
          msGridColumn: '3',
        }}
      >
        <Header />
        <Title />
        <RegistrationForm
          setMessageState={setMessageState}
          userDetails={userDetails}
          setUserDetails={setUserDetails}
          apiUrl={apiUrl}
        />
        <Text variant="privacyPolicy" mt={6} mb={7}>
          {t('EULATitle')}
          <Link
            sx={{
              textDecoration: 'none',
              color: 'electricBlue',
              '&hover': {
                color: 'electricBlueHover',
              },
            }}
            href="https://www.liveperson.com/policies/license-agreement/"
          >
            {t('EULA')}
          </Link>
        </Text>
      </Box>
    </Box>
  );
};

Home.propTypes = {
  apiUrl: PropTypes.string.isRequired,
  setMessageState: PropTypes.func.isRequired,
  userDetails: PropTypes.shape({
    firstName: PropTypes.string,
    lastName: PropTypes.string,
    password: PropTypes.string,
    region: PropTypes.object,
    email: PropTypes.string,
    company: PropTypes.string,
    role: PropTypes.object,
    intent: PropTypes.object,
  }).isRequired,
  setUserDetails: PropTypes.func.isRequired,
};

export default Home;
