import React, { Fragment } from 'react';
import { Box, Text, Image } from 'theme-ui';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { convertTextToLines } from '../utils';

const CarouselItem = ({ item, carouselWidth }) => {
  const { t } = useTranslation();

  const backgroundImage = `url('${process.env.PUBLIC_URL}/img/${item.bgImage}')`;

  return (
    <Box
      px={5}
      sx={{
        height: '100%',
        width: carouselWidth,
        backgroundImage,
        backgroundSize: 'cover',
        paddingTop: item.id === 'conversational-ai' ? 240 : 104,
        paddingBottom: 84,
        alignItems: 'center',
        overflow: 'auto',
      }}
    >
      {item.beforeImage && (
        <Image
          src={`${process.env.PUBLIC_URL}/img/${item.beforeImage}`}
          alt={item.altText}
          px={3}
          py={5}
          sx={{
            margin: '0 auto',
            display: 'block',
            width: item.id === 'channels' ? ['100%', '100%', '90%', '80%', '70%'] : ['100%', '100%', '50%', '40%'],
            height: 'auto',
          }}
        />
      )}
      {item.title && <Text as="div" variant="carouselTitle" mt={1}>{convertTextToLines(t(`${item.id}-title`))}</Text>}
      {item.text && <Text as="div" variant="carouselText" mt={1}>{convertTextToLines(t(`${item.id}-text`))}</Text>}
      {item.subtitle && <Text as="div" variant="carouselSubtitle" mt={1}>{convertTextToLines(t(`${item.id}-subtitle`))}</Text>}
      {item.author && (
        <>
          <Text as="div" variant="carouselAuthorName" mt={3}>{convertTextToLines(item.author.name)}</Text>
          <Text as="div" variant="carouselAuthorRole" mt={1}>{convertTextToLines(t(`${item.id}-role`))}</Text>
        </>
      )}
      {item.items && (
        <Box sx={{
          display: 'grid',
          gridTemplateColumns: '40px auto',
        }}
        >
          {item.items?.map((i) => (
            <Fragment key={i.icon}>
              <Text as="div" variant="carouselListIcon">{i.icon}</Text>
              <Text as="div" variant="carouselListItem" mt={4}>{t(i.label)}</Text>
            </Fragment>
          ))}
        </Box>
      )}
    </Box>
  );
};

CarouselItem.propTypes = {
  item: PropTypes.shape({
    id: PropTypes.string,
    author: PropTypes.shape({
      name: PropTypes.string,
      role: PropTypes.string,
    }),
    beforeImage: PropTypes.string,
    bgImage: PropTypes.string,
    maxHeight: PropTypes.number,
    minHeight: PropTypes.number,
    subtitle: PropTypes.string,
    text: PropTypes.string,
    title: PropTypes.string,
    altText: PropTypes.string,
    // eslint-disable-next-line react/forbid-prop-types
    items: PropTypes.array,
  }).isRequired,
  carouselWidth: PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.string,
  ]).isRequired,
};

export default CarouselItem;
