/* eslint-disable react/prop-types */
/* eslint-disable camelcase */
import React, {
  useEffect, useState, useRef, useLayoutEffect,
} from 'react';
import {
  Box, Text, Link as ThemeUILink,
} from 'theme-ui';
import Player from '@vimeo/player';
import { CompactSelectField, LoadingDots } from 'lp-react-components';
import PropTypes from 'prop-types';
import { useLocation, Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import Logo from './Logo';
import { ReactComponent as ErrorIcon } from '../assets/icon-error.svg';
import { ReactComponent as TimeoutIcon } from '../assets/icon-timeout.svg';
import { ReactComponent as CreatingIcon } from '../assets/icon-creating.svg';
import { ReactComponent as SuccessIcon } from '../assets/icon-success.svg';
import { convertTextToLines } from '../utils';
import theme from '../theme';

import languages from '../assets/languages.json';

const POLL_TIME = 5000;
const MAX_WAIT_TIME = 60000;
const MAX_ACCOUNT_CHECKS = Math.ceil(MAX_WAIT_TIME / POLL_TIME);
const IN_PROGRESS = 'IN_PROGRESS';
const SUCCESS = 'SUCCESS';

const getLanguage = (lang) => languages.find((l) => l.value === lang);

const getSize = (headerHeight, headerWidth, logoHeight) => {
  const newHeight = window.innerHeight - headerHeight - logoHeight - 96;
  if (newHeight / 0.5625 > headerWidth) {
    return {
      width: headerWidth,
      height: headerWidth * 0.5625,
    };
  }
  return {
    width: newHeight / 0.5625,
    height: newHeight,
  };
};

const CreatingAccount = ({
  messageState,
  accountDetails,
  resetRegistration,
  userDetails,
  setAccountDetails,
  setMessageState,
  apiUrl,
}) => {
  const { t, i18n } = useTranslation();

  const MESSAGE_STATES = {
    creating: {
      icon: <CreatingIcon style={{ width: 44, height: 44 }} />,
      title: t('CreatingTitle'),
      description: t('CreatingDescription'),
    },
    success: {
      icon: <SuccessIcon style={{ width: 44, height: 44 }} />,
      title: t('SuccessTitle'),
    },
    error: {
      icon: <ErrorIcon style={{ width: 44, height: 44 }} />,
      title: t('ErrorTitle'),
      description: t('ErrorDescription'),
      retryMessage: t('ErrorRetryMessage'),
    },
    timeout: {
      icon: <TimeoutIcon style={{ width: 44, height: 44 }} />,
      title: t('TimeoutTitle'),
      description: t('TimeoutDescription'),
      gettingStarted: t('TimeoutGettingStarted'),
    },
  };

  const [dimensions, setDimensions] = useState({});
  const playerRef = useRef();
  const headerRef = useRef();
  const logoRef = useRef();
  const playerContainer = useRef();
  const isSuccess = messageState === 'success';
  const isCreating = messageState === 'creating';
  const {
    icon, title, description, retryMessage, gettingStarted,
  } = MESSAGE_STATES[messageState];
  const location = useLocation();
  const creationId = location?.state?.creationId;

  useLayoutEffect(() => {
    const handleResize = () => {
      const headerHeight = headerRef.current.offsetHeight;
      const headerWidth = headerRef.current.offsetWidth;
      const logoHeight = logoRef.current.offsetHeight;
      setDimensions(getSize(headerHeight, headerWidth, logoHeight));
    };

    window.addEventListener('resize', handleResize);
    handleResize();

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, [messageState]);

  useEffect(() => {
    const getAccountStatus = (statusCheckCount = 0) => {
      fetch(`${apiUrl}/status/${creationId}`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Accept: 'application/json',
        },
      })
        .then((response) => response.json())
        .then((result) => {
          const { status, account_id } = result;
          if (status === SUCCESS) {
            setAccountDetails({ accountId: account_id, username: userDetails.email });
            setMessageState('success');
          } else if (status === IN_PROGRESS && statusCheckCount < MAX_ACCOUNT_CHECKS) {
            // Only restart the timer if we have not hit the max account checks
            setTimeout(() => getAccountStatus(statusCheckCount + 1), POLL_TIME);
          } else {
            setMessageState('timeout');
          }
        })
        .catch(() => setMessageState('error'));
    };

    getAccountStatus();
    playerRef.current = new Player('video');
    playerRef.current.ready().then(() => {
      playerRef.current.play().then().catch((e) => console.warn(e.message));
    });
  }, [setAccountDetails, setMessageState, userDetails, creationId, apiUrl]);

  return (
    <Box
      sx={{
        backgroundColor: 'navy',
        height: '100%',
        flex: 1,
        overflow: 'auto',
        display: 'grid',
        gridTemplateRows: 'auto 1fr',
        gridTemplateAreas: "'logo' 'content'",
        '@media all and (-ms-high-contrast: none), (-ms-high-contrast: active)': {
          display: '-ms-grid',
          msGridRows: 'auto 1fr',
        },
      }}
    >
      <Box mb={3} ref={logoRef} sx={{ gridArea: 'logo', msGridRow: '1' }}>
        <Link to={`${process.env.PUBLIC_URL}/`}>
          <Logo isStatic />
        </Link>
        <Box
          ml={3}
          sx={{
            position: 'absolute', top: theme.space[5], right: theme.space[3], width: 200,
          }}
        >
          <CompactSelectField
            name="language"
            value={getLanguage(i18n.language)}
            handleChange={(name, value) => i18n.changeLanguage(value)}
            placeholder={t('SelectCountry')}
            options={languages}
            height={300}
            variant="alt"
          />
        </Box>
      </Box>
      <Box
        mx={[4, 6, 7]}
        pb={6}
        px={[4, 6, 7]}
        sx={{
          alignItems: 'center',
          textAlign: 'center',
          height: '100%',
          gridArea: 'content',
          msGridRow: '2',
        }}
      >
        <Box ref={headerRef} width="100%" mb={6}>
          {icon}
          {title && (
            <Text variant="videoTitle" my={3}>
              <span style={{ paddingRight: isCreating ? 2 : 0 }}>{title}</span>
              {isCreating ? <LoadingDots pl={2} /> : null}
            </Text>
          )}
          {description && (
            <Text variant="videoDescription" mb={4} mx={[2, 4, 6]}>
              {convertTextToLines(description)}
              {retryMessage && (
                <Text
                  onClick={resetRegistration}
                  mt={4}
                  mb={4}
                  ml={1}
                  fontSize={1}
                  variant="retryText"
                >
                  {retryMessage}
                </Text>
              )}
            </Text>
          )}
          {gettingStarted && (
            <ThemeUILink
              href="https://developers.liveperson.com/getting-started-with-your-free-trial-account.html"
              target="_blank"
              mt={4}
              mb={6}
              ml={1}
              fontSize={1}
              variant="textLink"
            >
              {gettingStarted}
            </ThemeUILink>
          )}
          {isSuccess && (
            <Box
              mt={4}
              sx={{
                display: ['grid', 'grid', 'grid', 'flex'],
                gridGap: 3,
                alignItems: 'center',
                justifyContent: 'center',
                flexWrap: 'wrap',
              }}
            >
              <Box
                mb={[3, 3, 3, 0]}
                sx={{
                  display: ['grid', 'flex', 'flex'],
                  gridGap: 3,
                  overflow: 'hidden',
                }}
              >
                <Text variant="accountLabel" mr={[0, 0, 3]}>{t('AccountNumber')}</Text>
                <Text variant="accountDetail" mr={[0, 0, 6]}>{accountDetails.accountId}</Text>
                <Text variant="accountLabel" mr={[0, 0, 3]}>{t('Username')}</Text>
                <Text variant="accountDetail" mr={[0, 0, 0, 7]}>{accountDetails.username}</Text>
              </Box>
              <Box>
                <ThemeUILink href={`https://authentication.liveperson.net/login.html?accountId=${accountDetails.accountId}`} target="_blank" variant="primaryLink">{t('LoginToAccount')}</ThemeUILink>
              </Box>
            </Box>
          )}
        </Box>
        <Box sx={{ flex: 1, width: '100%' }} ref={playerContainer}>
          <Box
            sx={{
              margin: '0 auto',
              position: 'relative',
              height: dimensions.height,
              width: dimensions.width,
              outlineColor: 'shadow',
              outlineWidth: '6px',
              outlineStyle: 'solid',
            }}
          >
            <iframe
              title="FTUE"
              ref={playerRef}
              id="video"
              src="https://player.vimeo.com/video/398468660?autoplay=0&amp;app_id=122963"
              frameBorder="0"
              allow="autoplay; fullscreen"
              style={{
                position: 'absolute',
                top: 0,
                left: 0,
                width: '100%',
                height: '100%',
              }}
            />
          </Box>
        </Box>
      </Box>
    </Box>
  );
};

CreatingAccount.propTypes = {
  apiUrl: PropTypes.string.isRequired,
  messageState: PropTypes.string.isRequired,
  accountDetails: PropTypes.shape({
    accountId: PropTypes.string,
    username: PropTypes.string,
  }).isRequired,
  resetRegistration: PropTypes.func.isRequired,
};

export default CreatingAccount;
