/* eslint-disable no-unused-vars */
import React from 'react';
import {
  Box, Flex, Image, Text,
} from 'theme-ui';
import PropTypes from 'prop-types';
import { Trans, useTranslation } from 'react-i18next';

import { LoadingDots } from 'lp-react-components';
import Check from '../assets/icon-check.svg';

const ICON_SIZE = 18;

function ResendStatus({ resendOTP, resendSeconds, attempts }) {
  let children;
  const { t } = useTranslation();
  const resendAttempts = attempts > 0 ? t('resendOtpWithAttempt', { attempts }) : t('resendOtp');

  switch (resendSeconds) {
    case null:
      children = (
        <>
          <Image
            src={Check}
            alt="check"
            height={ICON_SIZE}
            width={ICON_SIZE}
            mr={3}
          />
          {t('otpSentEmail')}
        </>
      );
      break;
    case 0:
      children = (
        <Text
          sx={{
            fontSize: 0,
            WebkitFontSmoothing: 'antialiased',
            color: 'aqua',
            cursor: 'pointer',
            fontWeight: 700,
            '&:hover': {
              textDecoration: 'underline',
            },
          }}
          onClick={resendOTP}
        >
          {resendAttempts}
        </Text>

      );
      break;
    default:
      children = (
        <>
          <Text mr={1}>{t('resendOtpIn', { resendSeconds })}</Text>
          <Box sx={{ transform: 'scale(0.8) translateY(4px)' }}>
            <LoadingDots />
          </Box>
          {/* TODO: make these loading dots smaller by providing a size option */}
        </>
      );
      break;
  }

  return (
    <Flex
      mt={6}
      sx={{
        alignItems: 'center',
        fontSize: 0,
        WebkitFontSmoothing: 'antialiased',
        color: 'white',
        lineHeight: '20px',
      }}
    >
      {children}
    </Flex>
  );
}

ResendStatus.defaultProps = {
  resendSeconds: null,
};

ResendStatus.propTypes = {
  resendOTP: PropTypes.func.isRequired,
  resendSeconds: PropTypes.number,
  attempts: PropTypes.number.isRequired,
};

export default ResendStatus;
