/* eslint-disable react/forbid-prop-types */
/* eslint-disable camelcase */
/* global hj */

import React, { useState, useEffect, useCallback } from 'react';
import { Box } from 'theme-ui';
import {
  InputField, SelectField, Button, TextField,
} from 'lp-react-components';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { Redirect } from 'react-router-dom';
import Recaptcha from './Recaptcha';
import {
  validateEmail, getMarketingData,
} from '../utils';
import countries from '../assets/countries.json';

// This data is being used by the marketing team to track users using cookies
const MARKETING_DATA = getMarketingData(document.cookie);
const ALLOWED_CHARS = ', . \' -  # &';
const DUPLICATE_EMAIL = 'DUPLICATE_EMAIL';
const UNKNOWN_ERROR = 'UNKNOWN_ERROR';
const ROLES = ['developer', 'operations', 'marketing', 'executive', 'reseller', 'business', 'other'];
const SIGN_UP_INTENTS = ['integrate', 'message', 'chatbots', 'explore', 'other'];

const MENU_HEIGHT = 224;

const RegistrationForm = ({
  setMessageState, userDetails, setUserDetails, apiUrl,
}) => {
  const { t, i18n } = useTranslation();

  // This provides the initial error state, where there are no errors, but the messages are defined
  const getErrorData = useCallback(
    () => ({
      firstName: {
        message: t('RequiredField'),
      },
      lastName: {
        message: t('RequiredField'),
      },
      company: {
        message: t('RequiredField'),
        requirements: {
          data: [
            { name: t('invalidCompany') },
            { name: ALLOWED_CHARS },
          ],

        },
      },
      email: {
        message: t('InvalidBusiness'),
      },
      emailDuplicate: {
        message: t('DuplicateEmail'),
        isError: true,
        hasInteracted: true,
      },
      password: {
        message: t('InvalidPass'),
        requirements: {
          name: t('PassRuleTitle'),
          data: [
            { name: t('PassLenRule') },
            { name: t('PassUpperCaseRule') },
            { name: t('PassLowerCaseRule') },
            { name: t('PassNumberRule') },
            { name: t('PassSpecialChar') },
            { name: '& @ ! * $ < > / ( ) % # =', shouldIndent: true },
            { name: t('PassSpecialCharExep') },
          ],
        },
      },
      region: {
        message: t('RequiredField'),
      },
      role: {
        message: t('RequiredField'),
      },
      intent: {
        message: t('RequiredField'),
      },
      otherIntent: {
        message: t('RequiredField'),
      },
      recaptchaResponseToken: {
        message: t('RequiredField'),
      },
    }),
    [t],
  );

  const [errors, setErrors] = useState(getErrorData());
  const [isDuplicateEmail, setIsDuplicateEmail] = useState(false);
  const [creationId, setCreationId] = useState(false);

  const isOtherIntent = userDetails.intent?.value === 'other';

  const translateItem = (item, type) => ({ label: t(`${type}:${item}`), value: item });

  const sortByName = (a, b) => (a.name < b.name ? -1 : 1);

  const translateCountries = (c) => ({ ...c, label: i18n.t(`countries:${c.name}`) });

  const getCountries = () => [
    ...countries.map(translateCountries).sort(sortByName),
  ].filter((country) => !country.isBanned);

  const getCountry = () => getCountries().find(
    (country) => country.value === userDetails.region?.value,
  );

  const getRoles = () => ROLES.map((role) => translateItem(role, 'roles'));

  const getIntents = () => SIGN_UP_INTENTS.map((intent) => translateItem(intent, 'intents'));

  const updateError = (name, obj) => {
    setErrors((prevErrors) => ({
      ...prevErrors,
      [name]: {
        ...prevErrors[name],
        ...obj,
      },
    }));
  };
  const updateCompanyRequirement = (index, value) => {
    setErrors((prevErrors) => {
      const newErrors = { ...prevErrors };
      newErrors.company.requirements.data[index].isError = value;
      return newErrors;
    });
  };
  const updateRequirement = (index, value) => {
    setErrors((prevErrors) => {
      const newErrors = { ...prevErrors };
      newErrors.password.requirements.data[index].isError = value;
      return newErrors;
    });
  };

  const companyVerification = (value) => {
    const validCName = value.match(/[^[A-Za-z0-9 .,'&#-]+/) !== null;
    // if a special character is found thats not in the regex above
    // it is set to true else set to false
    updateCompanyRequirement(1, validCName);
    return validCName;
  };
  const isPasswordErrored = (value) => {
    // Check length
    const hasLength = value.length >= 8;
    updateRequirement(0, !hasLength);

    // Check uppercase
    const hasUppercase = value.match(/[A-Z]+/) !== null;
    updateRequirement(1, !hasUppercase);

    // Check lowercase
    const hasLowercase = value.match(/[a-z]+/) !== null;
    updateRequirement(2, !hasLowercase);

    // Check numbers
    const hasNumber = value.match(/[0-9]+/) !== null;
    updateRequirement(3, !hasNumber);

    // Check special characters
    const hasSpecial = value.match(/[&@!*$<>/()%#=]+/) !== null;
    updateRequirement(4, !hasSpecial);
    updateRequirement(5, !hasSpecial);

    // Check special characters
    const hasNoExtra = value.match(/^[(0-9)(a-z)(A-Z)&@!*$<>/()%#=]+$/) !== null;
    updateRequirement(6, !hasNoExtra);

    return !hasLength || !hasUppercase || !hasLowercase || !hasNumber || !hasSpecial || !hasNoExtra;
  };

  const validateUserDetail = (name, userDetail) => {
    let isError;

    switch (name) {
      case 'email':
        // There is an error if it is invalid, or if it's a duplicate email
        isError = isDuplicateEmail || !validateEmail(userDetail);
        break;
      case 'password':
        isError = isPasswordErrored(userDetail);
        break;
      case 'firstName':
      case 'lastName':
      case 'company':
        isError = userDetail.length < 1 || companyVerification(userDetail);
        break;
      case 'region':
      case 'role':
        isError = userDetail === null;
        break;
      case 'intent':
        isError = userDetail === null;
        break;
      case 'otherIntent':
        isError = userDetails.otherIntent === '' && isOtherIntent;
        break;
      default:
        isError = userDetail === '';
    }

    updateError(name, {
      hasInteracted: true,
      isError,
    });

    return isError;
  };

  const updateUserDetail = (name, userDetail) => {
    // If the email is changing, we should reset the duplicate email error
    if (name === 'email') {
      setIsDuplicateEmail(false);
      updateError(name, {
        hasInteracted: true,
        isError: false,
      });
    }
    setUserDetails({ ...userDetails, [name]: userDetail });
    validateUserDetail(name, userDetail);
  };

  const hasErrors = () => {
    const userDetailErrors = Object.keys(userDetails).map(
      (item) => validateUserDetail(item, userDetails[item]),
    );
    return userDetailErrors.reduce((item, acc) => acc || item, false);
  };

  const signUpForAccount = () => {
    // Remove unused properties from userDetails object
    const {
      intent, role, otherIntent, ...destructuredDetails
    } = userDetails;

    // Build signup body object
    const signUpBody = {
      ...MARKETING_DATA,
      ...destructuredDetails,
      region: userDetails.region.region,
      user_info: {
        role: userDetails.role.label,
        intent: otherIntent || userDetails.intent.label,
      },
    };

    fetch(`${apiUrl}/signup`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json',
      },
      body: JSON.stringify(signUpBody),
    })
      .then((response) => {
        if (response.status === 409) {
          throw new Error(DUPLICATE_EMAIL);
        }
        return response.json();
      })
      .then(({ code, description, accountCreationId }) => {
        if (accountCreationId) {
          setCreationId(accountCreationId);
        } else if (description) {
          throw new Error(description);
        } else if (code) {
          throw new Error(code);
        } else {
          throw new Error(UNKNOWN_ERROR);
        }
      })
      .catch((e) => {
        if (e.message === DUPLICATE_EMAIL) {
          // Account has already been created with this email
          setIsDuplicateEmail(true);
          updateError('email', { isError: true });
        } else {
          // NOTE: Why are we setting the error state here? What is the use case?
          setMessageState('error');
        }
        // Reset recaptcha after error
        window?.grecaptcha?.reset();
      });
  };

  const createAccount = (event) => {
    event.preventDefault();
    const hotJarIsEnabled = typeof hj === 'function';

    if (!hasErrors()) {
      signUpForAccount();
      if (hotJarIsEnabled) {
        hj('formSubmitSuccessful');
      }
    } else if (hotJarIsEnabled) {
      hj('formSubmitFailed');
    }
  };

  useEffect(() => {
    // NOTE: this will reset the errors completely if the language changes
    setErrors(getErrorData());
  }, [i18n.language, getErrorData]);

  if (creationId) {
    return (
      <Redirect
        push
        to={{
          pathname: `${process.env.PUBLIC_URL}/enter-passcode`,
          state: { creationId },
        }}
      />
    );
  }

  return (
    <Box sx={{ position: 'relative', maxWidth: 750 }}>
      <form data-testid="registerForm" autoComplete="off">
        <Box
          mb={3}
          sx={{
            display: 'grid',
            gridTemplateColumns: ['1fr 1fr', '2.5fr 2.5fr 4fr'],
            gridTemplateAreas: '\'firstname lastname\'',
            gridGap: 3,
            '@media all and (-ms-high-contrast: none), (-ms-high-contrast: active)': {
              display: '-ms-grid',
              msGridColumns: '2.5fr 2.5fr 4fr',
            },
          }}
        >
          <Box
            sx={{
              gridArea: 'firstname',
              msGridColumn: '1',
              '@media all and (-ms-high-contrast: none), (-ms-high-contrast: active)': {
                marginRight: 2,
              },
            }}
          >
            <InputField
              name="firstName"
              type="text"
              value={userDetails.firstName}
              handleChange={updateUserDetail}
              handleBlur={updateError}
              placeholder={t('FirstName')}
              error={errors.firstName}
              shouldFocusOnLoad
              isFullWidth
            />
          </Box>
          <Box sx={{
            gridArea: 'lastname',
            msGridColumn: '2',
            '@media all and (-ms-high-contrast: none), (-ms-high-contrast: active)': {
              marginLeft: 2,
            },
          }}
          >
            <InputField
              name="lastName"
              type="text"
              value={userDetails.lastName}
              handleChange={updateUserDetail}
              handleBlur={updateError}
              placeholder={t('LastName')}
              error={errors.lastName}
              isFullWidth
            />
          </Box>
        </Box>
        <Box mb={3}>
          <InputField
            name="company"
            type="text"
            value={userDetails.company}
            handleChange={updateUserDetail}
            handleBlur={updateError}
            placeholder={t('Company')}
            error={errors.company}
          />
        </Box>
        <Box mb={3}>
          <InputField
            name="email"
            type="email"
            value={userDetails.email}
            handleChange={updateUserDetail}
            handleBlur={updateError}
            placeholder={t('Email')}
            error={isDuplicateEmail ? errors.emailDuplicate : errors.email}
          />
        </Box>
        <Box mb={3}>
          <InputField
            name="password"
            type="password"
            value={userDetails.password}
            handleChange={updateUserDetail}
            handleBlur={updateError}
            placeholder={t('Password')}
            error={errors.password}
          />
        </Box>
        <Box mb={3}>
          <SelectField
            name="region"
            value={getCountry()}
            handleChange={updateUserDetail}
            handleBlur={updateError}
            error={errors.region}
            placeholder={t('SelectCountry')}
            options={getCountries()}
            optionsAbove
            height={MENU_HEIGHT}
          />
        </Box>
        <Box mb={3}>
          <SelectField
            name="role"
            value={userDetails.role}
            handleChange={updateUserDetail}
            handleBlur={updateError}
            error={errors.role}
            placeholder={t('SelectRole')}
            options={getRoles()}
            optionsAbove
            height={MENU_HEIGHT}
          />
        </Box>
        <Box mb={3}>
          <SelectField
            name="intent"
            value={userDetails.intent}
            handleChange={updateUserDetail}
            handleBlur={updateError}
            error={errors.intent}
            placeholder={t('SelectIntent')}
            options={getIntents()}
            optionsAbove
            height={MENU_HEIGHT}
            forceFocus={isOtherIntent}
          />
        </Box>
        {isOtherIntent && (
          <Box mb={3}>
            <TextField
              variant="disabled"
              name="otherIntent"
              value={userDetails.otherIntent}
              handleChange={updateUserDetail}
              handleBlur={updateError}
              placeholder={t('describeGoal')}
              error={errors.otherIntent}
            />
          </Box>
        )}
        <Recaptcha
          handleChange={updateUserDetail}
          error={errors.recaptchaResponseToken}
        />
        <Box
          pt={4}
          sx={{
            display: 'grid',
            gridTemplateColumns: ['auto', '5fr 4fr'],
            '@media all and (-ms-high-contrast: none), (-ms-high-contrast: active)': {
              display: '-ms-grid',
              msGridColumns: '5fr 4fr',
            },
          }}
        >
          <Button
            data-testid="registerButton"
            handleClick={createAccount}
            label={t('CreateAccount')}
            isFullWidth
          />
        </Box>
      </form>
    </Box>
  );
};

RegistrationForm.propTypes = {
  apiUrl: PropTypes.string.isRequired,
  setMessageState: PropTypes.func.isRequired,
  setUserDetails: PropTypes.func.isRequired,
  userDetails: PropTypes.shape({
    firstName: PropTypes.string,
    lastName: PropTypes.string,
    password: PropTypes.string,
    region: PropTypes.object,
    email: PropTypes.string,
    company: PropTypes.string,
    role: PropTypes.object,
    intent: PropTypes.object,
    otherIntent: PropTypes.string,
  }).isRequired,
};

export default RegistrationForm;
