import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';
import languageENUS from './languages/en-US.json';
import languageES from './languages/es.json';
import languageJP from './languages/ja-JP.json';
import languageBR from './languages/pt-BR.json';
import languageES419 from './languages/es-419.json';

i18n
  .use(LanguageDetector)
  .use(initReactI18next)
  .init({
    /* Set of available languages */
    resources: {
      'en-US': languageENUS,
      es: languageES,
      'ja-JP': languageJP,
      'es-419': languageES419,
      'pt-BR': languageBR,
    },
    /* Namespaces to load */
    ns: ['translations', 'countries'],
    /* Fallback language */
    fallbackLng: 'en-US',
    /* Namespace for language definitions */
    defaultNS: 'translations',
    /* Enable debugging for development environment */
    debug: process.env.NODE_ENV === 'development',
  });

export default i18n;
